@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:100,300,400,500,700,900&display=swap);
html,body,#root{width:100%;height:100%;margin:0;padding:0;font-family:'Noto Sans KR'}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Noto Sans KR', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;overflow-y:auto}#root{max-width:1600px;margin:auto;position:relative;display:flex}@media screen and (min-width: 1025px){#root{flex-direction:row}#root>div{margin-left:75px}}@media screen and (max-width: 1024px){#root{flex-direction:column}}p{margin:0;padding:0}h1,h2,h3,h4,h5{margin:0;padding:0}a{text-decoration:none;color:#fff}a:active{color:#fff}button{padding:0;margin:0;border-radius:0;background:none;border:none;outline:none}ul{list-style:none;margin:0;padding:0}ul li{margin:0;padding:0}*{outline:none}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

h1{margin:0;color:white}#root{overflow:hidden}.main-container{width:100%;height:100%;margin:0em;padding:0;z-index:10}

.main-container .background-container{width:100%;height:100%;position:fixed;top:0em;left:0;background-image:linear-gradient(10deg, #ef949d 50%, #9e5087 75%, #3d3d84 90%);background-position-y:50%;background-size:200% 200%;z-index:-10}.main-container .background-container #bgBuilding{position:absolute;bottom:-40%;left:-75%;width:250%;z-index:-9}.main-container .background-container #bgStars{position:absolute;width:100%;left:-40%;z-index:-10}.main-container .background-container #bgMoon{position:absolute;width:130px;height:130px;top:50%;margin-top:0px;left:-65px;z-index:-10}.main-container .background-container.loaded{transition:2s;background-position-y:0%}.main-container .background-container.loaded #bgBuilding{transition:2s;bottom:-25%;width:150%;left:-25%}.main-container .background-container.loaded #bgStars{left:0;transition:2s}.main-container .background-container.loaded #bgMoon{left:5%;margin-top:-20px;transition:2s}

@-webkit-keyframes arrow-animation{from{margin-bottom:-10px}to{margin-bottom:0}}@keyframes arrow-animation{from{margin-bottom:-10px}to{margin-bottom:0}}.search-form-container{width:100%;height:100%;display:flex;justify-content:center;align-items:center;flex-direction:column}.search-form-container .search-form-wrapper{width:280px;background-color:rgba(255,255,255,0.8);display:flex;flex-direction:row;align-items:center;justify-content:space-between;padding:5px 15px;border-radius:20px;box-shadow:3px 3px 5px rgba(0,0,0,0.2);margin-top:100%;opacity:0}.search-form-container .search-form-wrapper #search-input{margin:0 5px;background-color:rgba(0,0,0,0);border:0;color:#000;width:100%;height:100%;font-size:1.2rem;padding:3px 0}.search-form-container .search-form-wrapper #search-btn{border:0;background-color:rgba(0,0,0,0);color:#000;font-size:1.2rem;display:flex;justify-content:center;align-items:center}.search-form-container #search-help-text{margin-top:20px;font-size:0.8rem;color:rgba(255,255,255,0.7);opacity:0}.search-form-container .guide-container{margin-top:auto;display:flex;flex-direction:column;justify-content:center;align-items:center;padding:20px 0;width:100%;opacity:0}.search-form-container .guide-container .guide-arrow{font-size:1.1rem;color:rgba(255,255,255,0.7);-webkit-animation:arrow-animation 1s infinite;animation:arrow-animation 1s infinite}.search-form-container .guide-container .guide-text{font-size:1rem;color:rgba(255,255,255,0.7)}.search-form-container.loaded #search-help-text{transition:2s;opacity:1}.search-form-container.loaded .search-form-wrapper{transition:2s;opacity:1;margin-top:60%}.search-form-container.loaded .guide-container{transition:2s;opacity:1}

